<template>
  <article class="container-fluid pt-4 pb-5">
    <div class="row">
      <div class="col-12 col-md-10 col-lg-8 col-xl-6 mx-auto">
        <div class="jumbotron bg-white border p-5">
          <div class="container-fluid text-center">
            <h1 class="display-4">
              <fa icon="file-alt"></fa>&nbsp;Welcome to S3 Manager
            </h1>
            <p class="lead">An open-source, multi-server, client-side S3 manager.</p>
          </div>
        </div>

        <div class="bg-white border rounded p-4">
          <h1 class="h3 pb-2 border-bottom">
            <fa icon="cogs"></fa>&nbsp;Setup
          </h1>
          <p class="text-center">
            We securely encrypt the host connection details which are stored
            in your browsers local storage for added security. You will be required to re-enter the password when revisiting or reloading the page.
          </p>
          <p class="h5 text-center font-weight-bold border-bottom pb-2">Enter your desired password:</p>

          <form @submit.prevent="submit" no-validate>
            <div class="form-group">
              <label>Password</label>
              <input
                type="password"
                :class="['form-control', {'is-invalid': form.errors.password}]"
                v-model="form.values.password"
                placeholder="Enter a secure passwordâ€¦"
              />
              <div class="invalid-feedback" v-if="form.errors.password">{{ form.errors.password }}</div>
              <div
                class="text-muted small mt-1"
                v-if="!form.errors.password"
              >>= 8 characters, at least 1 upper &amp; lower case and a number or special character.</div>
            </div>
            <div class="form-group">
              <label>Repeat Password</label>
              <input
                type="password"
                :class="['form-control', {'is-invalid': form.errors.password_rpt}]"
                v-model="form.values.password_rpt"
                placeholder="Repeat your passwordâ€¦"
              />
              <div
                class="invalid-feedback"
                v-if="form.errors.password_rpt"
              >{{ form.errors.password_rpt }}</div>
            </div>
            <div class="form-group text-right">
              <button type="submit" class="btn btn-success">
                <fa icon="sign-in-alt"></fa>&nbsp;Login
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  </article>
</template>

<script>
import CryptoAES from 'crypto-js/aes'
import CryptoUTF8 from 'crypto-js/enc-utf8'

export default {
  name: "setup",
  props: ['state'],
  components: {},
  data: function () {
    return {
      form: {
        errors: {},
        values: {
          password: '',
          password_rpt: ''
        }
      }
    }
  },
  methods: {
    submit() {
      this.form.errors = {}

      //
      if (!this.form.values.password) {
        this.form.errors['password'] = 'Password is a required field.'
      } else if (!this.isValidPassword(this.form.values.password)) {
        this.form.errors['password'] = '>= 8 characters, at least 1 upper & lower case and a number or special character.'
      }

      //
      if (!this.form.values.password_rpt) {
        this.form.errors['password_rpt'] = 'Password repeat is a required field.'
      } else if (this.form.values.password_rpt !== this.form.values.password) {
        this.form.errors['password_rpt'] = 'Passwords do not match.'
      }

      if (Object.keys(this.form.errors).length) {
        return
      }

      const state = {
        date: new Date(),
        loading: true,
        authenticated: true,
        sidebar: {
          forced: false,
          open: true
        },
        errors: {},
        alert: {},
        form: {},
        hosts: {
          "play": {
            "url": "https://play.min.io:9000",
            "accessKey": "Q3AM3UQ867SPQQA43P2F",
            "secretKey": "zuf+tfteSlswRu7BJ86wekitnifILbZam1KYY3TG",
            "api": "S3v4",
            "lookup": "auto"
          },
          // "gcs": {
          //   "url": "https://storage.googleapis.com",
          //   "accessKey": "YOUR-ACCESS-KEY-HERE",
          //   "secretKey": "YOUR-SECRET-KEY-HERE",
          //   "api": "S3v2",
          //   "lookup": "dns"
          // },
          // "local": {
          //   "url": "http://localhost:9000",
          //   "accessKey": "",
          //   "secretKey": "",
          //   "api": "S3v4",
          //   "lookup": "auto"
          // },
          // "s3": {
          //   "url": "https://s3.amazonaws.com",
          //   "accessKey": "YOUR-ACCESS-KEY-HERE",
          //   "secretKey": "YOUR-SECRET-KEY-HERE",
          //   "api": "S3v4",
          //   "lookup": "dns"
          // }
        },
        config: {}
      }

      // set into state
      for (let key of Object.keys(state))
        this.$set(this.state, key, state[key])

      // encrypt and store into local storage
      localStorage.state = CryptoAES.encrypt(JSON.stringify(state), this.form.values.password).toString()
      //
      localStorage.auth_set = 1

      // set password into state for saving
      this.$set(this.state, 'password', this.form.values.password)

      this.$router.push('/')
    },
    isValidPassword(password) {
      var re = /(?=^.{8,256}$)((?=.*\d)|(?=.*\W+))(?![.\n])(?=.*[A-Z])(?=.*[a-z]).*/
      return re.test(password)
    }
  }
}
</script>

